h1 {
    color: green;
    font-style: italic;
  }

.note {
    color:grey;
    padding-top: 3px;
    font-size: 15px;
}

[notiftype = "error"] {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

[notiftype = "message"] {
    color: green;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}